.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}
.product-list-wrapper {
  position: relative;
}

.product {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.product-item {
  padding: 60px;
}

.show-more {
  text-align: center;
  padding-bottom: 20px;
}
a {
  color: #000;
}
.button-wraper {
  text-align: center;
}
