body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  align-items: center;
}
.product-list-wrapper {
  position: relative;
}

.product {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.product-item {
  padding: 60px;
}

.show-more {
  text-align: center;
  padding-bottom: 20px;
}
a {
  color: #000;
}
.button-wraper {
  text-align: center;
}

.phone-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding: 0 100px;
}

.phone {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}



.container {
  padding: 10px 100px;
  background-color: rgb(220, 243, 241);
  min-height: 1000px;
}
a {
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  height: 100%;
  display: block;
}

.notFound {
	text-align: center;
	padding: 20px;
}
.baskedItem {
  padding: 25px;
}
.baskedCard {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}
.totalSum {
  display: flex;
  justify-content: center;
}
.totalContent {
  padding: 10px;
}
.emptyBasket {
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


